	opacity: 1;
}

.flip-card {
  background-color: transparent;
  width: 280px;
  height: 220px;
  margin: 0 auto;
}

.flip-card-inner {
  position: relative;
  width: 100%;
  height: 100%;
  text-align: center;
  transition: -webkit-transform 0.6s;
  transition: transform 0.6s;
  transition: transform 0.6s, -webkit-transform 0.6s;
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
  
}

/* ROTATING DIV 180deg to make ilussion that card has appeared with content */
.flip-card:hover .flip-card-inner {
  -webkit-transform: rotateY(180deg);
          transform: rotateY(180deg);
}

.flip-card-front, .flip-card-back {
  position: absolute;
  width: 100%;
  height: 100%;
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
}

.flip-card-front {
  background-color: transparent;
  color: red;
  z-index: 2; 
}

/*In this div the content exists */
.flip-card-back {
  background-color: black;
  border: 2px solid white;
  color: white;
  -webkit-transform: rotateY(180deg);
          transform: rotateY(180deg);
  z-index: 1;
}

/* Heart icon as the front page of the card before flipped */
.fa-heart {
	color: red;
	position: relative;
	margin-top: 35%;
